<!--  -->
<template>
  <div class="recommend" ref="recommend">
    <scroll ref="scroll" :data="discList" class="recommend-content">
      <div>
        <div v-if="slider.length" class="slider-wrapper">
          <slider>
            <div v-for="item in slider" :key="item.id">
              <a :href="item.linkUrl">
                <img :src="item.picUrl" class="neddsclick" @load="loadImage">
              </a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <h1 class="list-title">热门歌单推荐</h1>
          <ul>
            <li v-for="(item, index) in discList" class="item" :key="index">
              <div class="icon">
                <img width="60" height="60" v-lazy="item.imgurl">
              </div>
              <div class="text">
                <h2 class="name" v-html="item.creator.name"></h2>
                <p class="desc" v-html="item.dissname"></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!discList.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script>
import { getRecommend, getTopList } from 'api/recommend'
import { ERR_OK } from 'api/config'
import Slider from 'base/slider/slider'
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
export default {
  data() {
    return {
      slider: [],
      discList: []
    }
  },
  created() {
    this._getRecommend()
    this._getTopList()
  },
  components: {
    Slider,
    Scroll,
    Loading
  },

  methods: {
    _getRecommend() {
      getRecommend().then(res => {
        if (res.code === ERR_OK) {
          this.slider = res.data.slider
        }
      })
    },
    _getTopList() {
        getTopList().then((res) => {
          if (res.code === ERR_OK) {
            this.discList = res.data.list
          }
        })
    },
    loadImage() {
      if (!this.checkload) {
        this.checkload = true
        this.$refs.scroll.refresh()
      }
    }
  }
}
</script>

<style lang='stylus' rel='stylesheet/stylus' scoped>
@import "~common/stylus/variable"
.recommend
  position fixed
  width 100%
  top 88px
  bottom: 0
  .recommend-content
    height 100%
    overflow hidden
    .slider-wrapper
      position relative
      width 100%
      overflow hidden
    .recommend-list
      .list-title
        height 65px
        line-height 65px
        text-align center
        font-size $font-size-medium
        color $color-theme
      .item
        display flex
        box-sizing border-box
        align-items center
        padding 0 20px 20px 20px
        .icon
          flex 0 0 60px
          width 60px
          padding-right 20px
        .text
          display flex
          flex-direction column
          justify-content center
          flex 1
          line-height 20px
          overflow hidden
          font-size $font-size-medium
          .name
            margin-bottom 10px
            color $color-text
          .desc
            color $color-text-d
  .loading-container
    position absolute
    width 100%
    top 50%
    transform translateY(-50%)
</style>


